<template>
	<div @click="goToTagPage">
		<audio id="myAudio" src="../assets/audio/首页音频.mp3" autoplay muted loop></audio>
		<video :src="videoUrl" autoplay muted loop>
		</video>
	</div>
</template>

<script>
export default {
	name: 'HelloWorld',
	data() {
		return {
			clickNum: 0,
			videoUrl: require("../assets/video/1.mp4")
		}
	},
	methods: {
		goToTagPage() {
			this.clickNum += 1
			if (this.clickNum == 1) {
				this.videoUrl = require("../assets/video/项目介绍1.mp4")
			}else if(this.clickNum == 2){
				this.videoUrl = require("../assets/video/项目介绍2.mp4")
			}else{
				this.$router.push({ name: 'tags' })
			}
			
		}
	}
}
</script>

<style>
video {
	box-sizing: border-box;
  width: 100%;
	height: 1440;
}

body {
	cursor: url('../assets/image/cursor.ico'), pointer !important;
}
</style>
